import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Chip,
  AppBar,
  Toolbar,
  IconButton,
  Tooltip,
  Tabs,
  Tab,
  Grid,
  Card,
  CardContent,
  CardActions,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions
} from '@mui/material';
import {
  ArrowBack as ArrowBackIcon,
  Download as DownloadIcon,
  FilterList as FilterIcon,
  Assessment as AssessmentIcon,
  TableChart as TableChartIcon,
  Visibility as VisibilityIcon,
  Notifications as NotificationsIcon
} from '@mui/icons-material';
import { useAuth } from '../../contexts/AuthContext';
import toast from 'react-hot-toast';

interface FormularioResultado {
  id: string;
  banco: string;
  formulario: string;
  fechaCompletado: string;
  estado: 'completado' | 'pendiente' | 'vencido';
  datos: any;
  comentarios?: { [key: string]: string };
}

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tabpanel-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

const VerResultados: React.FC = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [tabValue, setTabValue] = useState(0);
  const [filtroBanco, setFiltroBanco] = useState('');
  const [filtroFormulario, setFiltroFormulario] = useState('');
  const [filtroEstado, setFiltroEstado] = useState('');
  const [mostrarFormulario, setMostrarFormulario] = useState(false);
  const [formularioSeleccionado, setFormularioSeleccionado] = useState<any>(null);
  const [comentarios, setComentarios] = useState<{ [key: string]: string }>({});
  const [modalEmail, setModalEmail] = useState(false);
  const [emailData, setEmailData] = useState({
    asunto: '',
    destinatario: '',
    cuerpo: ''
  });

  const [resultados] = useState<FormularioResultado[]>([
    {
      id: '1',
      banco: 'Banco de Chile',
      formulario: 'Apoyo - Corredores/Asset Management/Wealth Management',
      fechaCompletado: '2024-12-01',
      estado: 'completado',
      datos: { 
        'AP-010': 'GERENTE DE DIVISIN OPERACIONES Y TECNOLOGA',
        'AP-020': 'GERENTE OPERACIONES AM',
        'AP-021': 'SUBGERENTE /JEFE DE OPERACIONES AM',
        'Valor 1': '1836552',
        'Valor 2': '2.722493',
        'Valor 3': '5000000'
      }
    },
    {
      id: '2',
      banco: 'Banco Santander',
      formulario: 'Negociacin Internacional y Comercio',
      fechaCompletado: '2024-11-28',
      estado: 'completado',
      datos: {
        'W-00': {
          codigo: 'W-00',
          nombre: 'HEAD NEGOCIO FINANCIERO',
          nivel: 'MERCER',
          bono: '1000',
          incentivos: '4',
          total: '40000',
          fechaNacimiento: '23-01-1996',
          genero: 'masculino'
        },
        'W-01': {
          codigo: 'W-01',
          nombre: 'GERENTE BANCA MAYORISTA',
          nivel: 'GGS',
          bono: '2000',
          incentivos: '4',
          total: '80000',
          fechaNacimiento: '23-01-1996',
          genero: 'masculino'
        },
        'F-00': {
          codigo: 'F-00',
          nombre: 'GERENTE DIVISIN TESORERA / GERENTE DIVISIN MERCADO DE CAPITALES',
          nivel: 'HAY',
          bono: '3000',
          incentivos: '4',
          total: '120000',
          fechaNacimiento: '23-01-1996',
          genero: 'masculino'
        }
      }
    },
    {
      id: '3',
      banco: 'Banco de Chile',
      formulario: 'BP Wealth Management',
      fechaCompletado: '',
      estado: 'pendiente',
      datos: {}
    }
  ]);

  const bancos = ['Todos', 'Banco de Chile', 'Banco Santander', 'Banco Estado'];
  const formularios = ['Todos', 'Apoyo - Corredores/Asset Management/Wealth Management', 'Negociacin Internacional y Comercio', 'BP Wealth Management', 'Corredora', 'Asset Management'];
  const estados = ['Todos', 'completado', 'pendiente', 'vencido'];

  const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
    setTabValue(newValue);
  };

  const filtrarResultados = () => {
    return resultados.filter(resultado => {
      const cumpleBanco = filtroBanco === 'Todos' || resultado.banco === filtroBanco;
      const cumpleFormulario = filtroFormulario === 'Todos' || resultado.formulario === filtroFormulario;
      const cumpleEstado = filtroEstado === 'Todos' || resultado.estado === filtroEstado;
      
      return cumpleBanco && cumpleFormulario && cumpleEstado;
    });
  };

  const handleDescargarExcel = (tipo: 'individual' | 'consolidado', id?: string) => {
    if (tipo === 'individual' && id) {
      const resultado = resultados.find(r => r.id === id);
      if (resultado?.formulario === 'Negociacin Internacional y Comercio') {
        // Descargar el archivo neg_ic.xlsx
        const link = document.createElement('a');
        link.href = '/neg_ic.xlsx';
        link.download = 'neg_ic.xlsx';
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        toast.success('Descargando formulario Negociacin Internacional y Comercio...');
      } else {
        toast.success(`Descargando formulario ${id} en Excel...`);
      }
    } else {
      toast.success('Descargando consolidado en Excel...');
    }
  };

  const handleEnviarNotificacion = (resultado: FormularioResultado) => {
    // Preparar datos del email con los comentarios
    const comentariosTexto = Object.entries(comentarios)
      .filter(([_, comentario]) => comentario.trim() !== '')
      .map(([codigo, comentario]) => `${codigo}: ${comentario}`)
      .join('\n');
    
    setEmailData({
      asunto: `Comentarios para ${resultado.formulario} - ${resultado.banco}`,
      destinatario: `contacto@${resultado.banco.toLowerCase().replace(/\s+/g, '')}.cl`,
      cuerpo: `Estimados,\n\nA continuacin los comentarios para el formulario "${resultado.formulario}":\n\n${comentariosTexto}\n\nSaludos cordiales,\nEquipo de Administracin`
    });
    setModalEmail(true);
  };

  const handleVisualizarFormulario = (resultado: FormularioResultado) => {
    setFormularioSeleccionado(resultado);
    setMostrarFormulario(true);
    // Inicializar comentarios si no existen
    if (!resultado.comentarios) {
      setComentarios({});
    } else {
      setComentarios(resultado.comentarios);
    }
  };

  const handleComentarioChange = (filaId: string, comentario: string) => {
    setComentarios(prev => ({
      ...prev,
      [filaId]: comentario
    }));
  };

  const handleGuardarComentarios = () => {
    // Simular guardado en base de datos
    const comentariosGuardados = Object.entries(comentarios).filter(([_, comentario]) => comentario.trim() !== '');
    
    if (comentariosGuardados.length === 0) {
      toast.error('No hay comentarios para guardar');
      return;
    }
    
    // Aqu se enviaran los comentarios a la API
    console.log('Guardando comentarios:', comentarios);
    toast.success(`${comentariosGuardados.length} comentarios guardados exitosamente`);
  };

  const handleEnviarEmail = () => {
    // Simular envo de email
    console.log('Enviando email:', emailData);
    toast.success('Email enviado exitosamente');
    setModalEmail(false);
    setEmailData({ asunto: '', destinatario: '', cuerpo: '' });
  };

  const getEstadoColor = (estado: string) => {
    switch (estado) {
      case 'completado': return 'success';
      case 'pendiente': return 'warning';
      case 'vencido': return 'error';
      default: return 'default';
    }
  };

  const resultadosFiltrados = filtrarResultados();

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <IconButton color="inherit" onClick={() => navigate('/admin')}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Ver Resultados
          </Typography>
        </Toolbar>
      </AppBar>

      <Container maxWidth="xl" sx={{ mt: 4, mb: 4 }}>
        <Typography variant="h4" gutterBottom>
          Resultados de Formularios
        </Typography>

        {/* Filtros */}
        <Paper className="filtros-container">
          <Typography variant="h6" gutterBottom sx={{ mb: 2 }}>
            <FilterIcon sx={{ mr: 1, verticalAlign: 'middle' }} />
            Filtros
          </Typography>
          
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6} md={3}>
              <FormControl fullWidth>
                <InputLabel>Banco</InputLabel>
                <Select
                  value={filtroBanco}
                  label="Banco"
                  onChange={(e) => setFiltroBanco(e.target.value)}
                >
                  {bancos.map((banco) => (
                    <MenuItem key={banco} value={banco}>
                      {banco}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} sm={6} md={3}>
              <FormControl fullWidth>
                <InputLabel>Formulario</InputLabel>
                <Select
                  value={filtroFormulario}
                  label="Formulario"
                  onChange={(e) => setFiltroFormulario(e.target.value)}
                >
                  {formularios.map((formulario) => (
                    <MenuItem key={formulario} value={formulario}>
                      {formulario}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} sm={6} md={3}>
              <FormControl fullWidth>
                <InputLabel>Estado</InputLabel>
                <Select
                  value={filtroEstado}
                  label="Estado"
                  onChange={(e) => setFiltroEstado(e.target.value)}
                >
                  {estados.map((estado) => (
                    <MenuItem key={estado} value={estado}>
                      {estado === 'Todos' ? 'Todos' : estado.charAt(0).toUpperCase() + estado.slice(1)}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Grid>
            
            <Grid item xs={12} sm={6} md={3}>
              <Button
                variant="contained"
                fullWidth
                onClick={() => {
                  setFiltroBanco('');
                  setFiltroFormulario('');
                  setFiltroEstado('');
                }}
                sx={{ height: '56px' }}
              >
                Limpiar Filtros
              </Button>
            </Grid>
          </Grid>
        </Paper>

        {/* Tabs para diferentes vistas */}
        <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
          <Tabs value={tabValue} onChange={handleTabChange} aria-label="vistas de resultados">
            <Tab 
              label="Vista por Formulario" 
              icon={<TableChartIcon />} 
              iconPosition="start"
            />
            <Tab 
              label="Vista Consolidada" 
              icon={<AssessmentIcon />} 
              iconPosition="start"
            />
          </Tabs>
        </Box>

        {/* Tab Panel: Vista por Formulario */}
        <TabPanel value={tabValue} index={0}>
          <Box sx={{ mb: 3, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="h6">
              Resultados por Formulario ({resultadosFiltrados.length})
            </Typography>
            <Button
              variant="outlined"
              startIcon={<DownloadIcon />}
              onClick={() => handleDescargarExcel('consolidado')}
            >
              Descargar Consolidado
            </Button>
          </Box>

          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Banco</TableCell>
                  <TableCell>Formulario</TableCell>
                  <TableCell>Estado</TableCell>
                  <TableCell>Fecha Completado</TableCell>
                  <TableCell>Acciones</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {resultadosFiltrados.map((resultado) => (
                  <TableRow key={resultado.id}>
                    <TableCell>{resultado.banco}</TableCell>
                    <TableCell>{resultado.formulario}</TableCell>
                    <TableCell>
                      <Chip 
                        label={resultado.estado.charAt(0).toUpperCase() + resultado.estado.slice(1)}
                        color={getEstadoColor(resultado.estado) as any}
                        size="small"
                      />
                    </TableCell>
                    <TableCell>
                      {resultado.fechaCompletado || '-'}
                    </TableCell>
                    <TableCell>
                      <Box sx={{ display: 'flex', gap: 1 }}>
                        <Button
                          size="small"
                          variant="outlined"
                          startIcon={<VisibilityIcon />}
                          onClick={() => handleVisualizarFormulario(resultado)}
                          color="info"
                        >
                          Visualizar
                        </Button>
                        <Button
                          size="small"
                          variant="outlined"
                          startIcon={<DownloadIcon />}
                          onClick={() => handleDescargarExcel('individual', resultado.id)}
                          disabled={resultado.estado !== 'completado'}
                        >
                          Descargar
                        </Button>
                      </Box>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </TabPanel>

        {/* Tab Panel: Vista Consolidada */}
        <TabPanel value={tabValue} index={1}>
          <Box sx={{ mb: 3, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="h6">
              Vista Consolidada - Formulario NEG. IC
            </Typography>
            <Box sx={{ display: 'flex', gap: 2 }}>
              <Button
                variant="outlined"
                onClick={handleGuardarComentarios}
              >
                Guardar Comentarios
              </Button>
              <Button
                variant="contained"
                startIcon={<DownloadIcon />}
                onClick={() => handleDescargarExcel('consolidado')}
              >
                Descargar Excel Consolidado
              </Button>
            </Box>
          </Box>

          {/* Tabla consolidada del formulario NEG. IC */}
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell><strong>Banco</strong></TableCell>
                  <TableCell><strong>CD. CARGO</strong></TableCell>
                  <TableCell><strong>NOMBRE GENRICO DEL CARGO</strong></TableCell>
                  <TableCell><strong>NIVEL DEL CARGO (HAY, MERCER O GGS)</strong></TableCell>
                  <TableCell><strong>$ BONO ANUAL PAGADO EL 2025 POR EL DESEMPEO DEL 2024</strong></TableCell>
                  <TableCell><strong>INCENTIVOS TRIMESTRALES/ MENSUALES PAGADOS EL 2024</strong></TableCell>
                  <TableCell><strong>$ TOTAL COMISIONES PAGADAS EL 2024</strong></TableCell>
                  <TableCell><strong>FECHA DE NACIMIENTO (dd/mm/aaaa)</strong></TableCell>
                  <TableCell><strong>GNERO (Masculino  Femenino)</strong></TableCell>
                  <TableCell><strong>Comentario</strong></TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {/* Datos del formulario NEG. IC de Banco Santander */}
                <TableRow>
                  <TableCell>Banco Santander</TableCell>
                  <TableCell>W-00</TableCell>
                  <TableCell>HEAD NEGOCIO FINANCIERO</TableCell>
                  <TableCell>MERCER</TableCell>
                  <TableCell>1000</TableCell>
                  <TableCell>4</TableCell>
                  <TableCell>40000</TableCell>
                  <TableCell>23-01-1996</TableCell>
                  <TableCell>masculino</TableCell>
                  <TableCell>
                    <TextField
                      size="small"
                      placeholder="Agregar comentario..."
                      value={comentarios['W-00'] || ''}
                      onChange={(e) => handleComentarioChange('W-00', e.target.value)}
                      fullWidth
                    />
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>Banco Santander</TableCell>
                  <TableCell>W-01</TableCell>
                  <TableCell>GERENTE BANCA MAYORISTA</TableCell>
                  <TableCell>GGS</TableCell>
                  <TableCell>2000</TableCell>
                  <TableCell>4</TableCell>
                  <TableCell>80000</TableCell>
                  <TableCell>23-01-1996</TableCell>
                  <TableCell>masculino</TableCell>
                  <TableCell>
                    <TextField
                      size="small"
                      placeholder="Agregar comentario..."
                      value={comentarios['W-01'] || ''}
                      onChange={(e) => handleComentarioChange('W-01', e.target.value)}
                      fullWidth
                    />
                  </TableCell>
                </TableRow>
                <TableRow>
                  <TableCell>Banco Santander</TableCell>
                  <TableCell>F-00</TableCell>
                  <TableCell>GERENTE DIVISIN TESORERA / GERENTE DIVISIN MERCADO DE CAPITALES</TableCell>
                  <TableCell>HAY</TableCell>
                  <TableCell>3000</TableCell>
                  <TableCell>4</TableCell>
                  <TableCell>120000</TableCell>
                  <TableCell>23-01-1996</TableCell>
                  <TableCell>masculino</TableCell>
                  <TableCell>
                    <TextField
                      size="small"
                      placeholder="Agregar comentario..."
                      value={comentarios['F-00'] || ''}
                      onChange={(e) => handleComentarioChange('F-00', e.target.value)}
                      fullWidth
                    />
                  </TableCell>
                </TableRow>
              </TableBody>
            </Table>
          </TableContainer>
        </TabPanel>

        {resultadosFiltrados.length === 0 && (
          <Box sx={{ textAlign: 'center', mt: 4 }}>
            <Typography variant="h6" color="textSecondary">
              No se encontraron resultados con los filtros aplicados
            </Typography>
          </Box>
        )}

        {/* Modal para visualizar formulario */}
        <Dialog 
          open={mostrarFormulario} 
          onClose={() => setMostrarFormulario(false)}
          maxWidth="xl"
          fullWidth
        >
          <DialogTitle sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="h6">
              Visualizar Formulario: {formularioSeleccionado?.formulario}
            </Typography>
          </DialogTitle>
          <DialogContent>
            {formularioSeleccionado && (
              <Box sx={{ mt: 2 }}>
                <Grid container spacing={2}>
                  <Grid item xs={12} md={6}>
                    <Typography variant="subtitle1" gutterBottom>
                      <strong>Banco:</strong> {formularioSeleccionado.banco}
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <Typography variant="subtitle1" gutterBottom>
                      <strong>Estado:</strong> 
                      <Chip 
                        label={formularioSeleccionado.estado.charAt(0).toUpperCase() + formularioSeleccionado.estado.slice(1)}
                        color={getEstadoColor(formularioSeleccionado.estado) as any}
                        size="small"
                        sx={{ ml: 1 }}
                      />
                    </Typography>
                  </Grid>
                  <Grid item xs={12} md={6}>
                    <Typography variant="subtitle1" gutterBottom>
                      <strong>Fecha de Completado:</strong> {formularioSeleccionado.fechaCompletado || 'Pendiente'}
                    </Typography>
                  </Grid>
                </Grid>
                
                <Typography variant="h6" sx={{ mt: 3, mb: 2 }}>
                  Datos del Formulario
                </Typography>
                
                                 {/* Mostrar datos del formulario de Negociacin Internacional */}
                 {formularioSeleccionado?.formulario === 'Negociacin Internacional y Comercio' ? (
                   <TableContainer component={Paper}>
                     <Table>
                       <TableHead>
                         <TableRow>
                           <TableCell><strong>CD. CARGO</strong></TableCell>
                           <TableCell><strong>NOMBRE GENRICO DEL CARGO</strong></TableCell>
                           <TableCell><strong>NIVEL DEL CARGO (HAY, MERCER O GGS)</strong></TableCell>
                           <TableCell><strong>$ BONO ANUAL PAGADO EL 2025 POR EL DESEMPEO DEL 2024</strong></TableCell>
                           <TableCell><strong>INCENTIVOS TRIMESTRALES/ MENSUALES PAGADOS EL 2024</strong></TableCell>
                           <TableCell><strong>$ TOTAL COMISIONES PAGADAS EL 2024</strong></TableCell>
                           <TableCell><strong>FECHA DE NACIMIENTO (dd/mm/aaaa)</strong></TableCell>
                           <TableCell><strong>GNERO (Masculino  Femenino)</strong></TableCell>
                           <TableCell><strong>Comentario</strong></TableCell>
                         </TableRow>
                       </TableHead>
                       <TableBody>
                         {Object.values(formularioSeleccionado.datos).map((fila: any, index) => (
                           <TableRow key={index}>
                             <TableCell>{fila.codigo}</TableCell>
                             <TableCell>{fila.nombre}</TableCell>
                             <TableCell>{fila.nivel}</TableCell>
                             <TableCell>{fila.bono}</TableCell>
                             <TableCell>{fila.incentivos}</TableCell>
                             <TableCell>{fila.total}</TableCell>
                             <TableCell>{fila.fechaNacimiento}</TableCell>
                             <TableCell>{fila.genero}</TableCell>
                             <TableCell>
                               <TextField
                                 size="small"
                                 placeholder="Agregar comentario..."
                                 value={comentarios[fila.codigo] || ''}
                                 onChange={(e) => handleComentarioChange(fila.codigo, e.target.value)}
                                 fullWidth
                               />
                             </TableCell>
                           </TableRow>
                         ))}
                       </TableBody>
                     </Table>
                   </TableContainer>
                 ) : (
                   <TableContainer component={Paper}>
                     <Table>
                       <TableHead>
                         <TableRow>
                           <TableCell><strong>Cdigo</strong></TableCell>
                           <TableCell><strong>Descripcin</strong></TableCell>
                           <TableCell><strong>Valor 1</strong></TableCell>
                           <TableCell><strong>Valor 2</strong></TableCell>
                           <TableCell><strong>Valor 3</strong></TableCell>
                           <TableCell><strong>Comentario</strong></TableCell>
                         </TableRow>
                       </TableHead>
                       <TableBody>
                         <TableRow>
                           <TableCell colSpan={6} align="center">
                             <Typography color="textSecondary">
                               Datos del formulario no disponibles
                             </Typography>
                           </TableCell>
                         </TableRow>
                       </TableBody>
                     </Table>
                   </TableContainer>
                 )}
              </Box>
            )}
          </DialogContent>
                     <DialogActions>
             <Button onClick={() => setMostrarFormulario(false)}>Cerrar</Button>
             <Button 
               variant="outlined"
               onClick={handleGuardarComentarios}
             >
               Guardar Comentarios
             </Button>
             <Button 
               variant="contained"
               color="error"
               startIcon={<NotificationsIcon />}
               onClick={() => {
                 if (formularioSeleccionado) {
                   handleEnviarNotificacion(formularioSeleccionado);
                 }
               }}
               disabled={formularioSeleccionado?.estado !== 'completado'}
             >
               Enviar Comentarios
             </Button>
             <Button 
               variant="contained"
               startIcon={<DownloadIcon />}
               onClick={() => {
                 handleDescargarExcel('individual', formularioSeleccionado?.id);
                 setMostrarFormulario(false);
               }}
               disabled={formularioSeleccionado?.estado !== 'completado'}
             >
               Descargar Excel
             </Button>
           </DialogActions>
        </Dialog>

        {/* Modal Email */}
        <Dialog open={modalEmail} onClose={() => setModalEmail(false)} maxWidth="md" fullWidth>
          <DialogTitle>
            Enviar Comentarios por Email
          </DialogTitle>
          <DialogContent>
            <Box sx={{ mt: 2 }}>
              <Grid container spacing={2}>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Asunto"
                    value={emailData.asunto}
                    onChange={(e) => setEmailData({...emailData, asunto: e.target.value})}
                    variant="outlined"
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Destinatario"
                    value={emailData.destinatario}
                    onChange={(e) => setEmailData({...emailData, destinatario: e.target.value})}
                    variant="outlined"
                    type="email"
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField
                    fullWidth
                    label="Cuerpo del mensaje"
                    value={emailData.cuerpo}
                    onChange={(e) => setEmailData({...emailData, cuerpo: e.target.value})}
                    variant="outlined"
                    multiline
                    rows={8}
                  />
                </Grid>
              </Grid>
            </Box>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setModalEmail(false)}>Cancelar</Button>
            <Button 
              onClick={handleEnviarEmail}
              variant="contained"
              color="primary"
              startIcon={<NotificationsIcon />}
            >
              Enviar Email
            </Button>
          </DialogActions>
        </Dialog>
      </Container>
    </Box>
  );
};

export default VerResultados;
