import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  Container,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Button,
  TextField,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  IconButton,
  Tooltip,
  Chip,
  AppBar,
  Toolbar,
  Switch,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Grid,
  Card,
  CardContent,
  CardActions,
  Alert,
  Checkbox,
  FormControlLabel,
  Accordion,
  AccordionSummary,
  AccordionDetails
} from '@mui/material';
import {
  ArrowBack as ArrowBackIcon,
  Add as AddIcon,
  Edit as EditIcon,
  Delete as DeleteIcon,
  Upload as UploadIcon,
  ExpandMore as ExpandMoreIcon,
  Description as DescriptionIcon,
  Settings as SettingsIcon,
  CloudUpload as CloudUploadIcon
} from '@mui/icons-material';
import { useAuth } from '../../contexts/AuthContext';
import toast from 'react-hot-toast';

interface ColumnConfig {
  id: string;
  nombre: string;
  tipo: 'texto' | 'numero' | 'fecha' | 'genero';
  requerido: boolean;
  editable: boolean;
}

interface Formulario {
  id: string;
  nombre: string;
  descripcion: string;
  fechaCreacion: string;
  fechaLimite: string;
  activo: boolean;
  editableByBanco: boolean;
  columnas?: ColumnConfig[];
  asignaciones: {
    bancoId: string;
    bancoNombre: string;
    activo: boolean;
    editableByBanco: boolean;
  }[];
}

const GestionFormularios: React.FC = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  
  const [formularios, setFormularios] = useState<Formulario[]>([
    {
      id: '1',
      nombre: 'Apoyo - Corredores/Asset Management/Wealth Management',
      descripcion: 'Levantamiento de cargos de apoyo para corredores, asset management y wealth management',
      fechaCreacion: '2024-01-15',
      fechaLimite: '2024-12-31',
      activo: true,
      editableByBanco: false,
      asignaciones: [
        { bancoId: '1', bancoNombre: 'Banco de Chile', activo: true, editableByBanco: false },
        { bancoId: '2', bancoNombre: 'Banco Santander', activo: true, editableByBanco: false },
        { bancoId: '3', bancoNombre: 'Banco Estado', activo: false, editableByBanco: false }
      ]
    },
    {
      id: '3',
      nombre: 'BP Wealth Management',
      descripcion: 'Formulario especfico para BP Wealth Management',
      fechaCreacion: '2024-03-01',
      fechaLimite: '2024-10-31',
      activo: true,
      editableByBanco: false,
      asignaciones: [
        { bancoId: '1', bancoNombre: 'Banco de Chile', activo: true, editableByBanco: false },
        { bancoId: '2', bancoNombre: 'Banco Santander', activo: true, editableByBanco: false },
        { bancoId: '3', bancoNombre: 'Banco Estado', activo: false, editableByBanco: false }
      ]
    },
    {
      id: '4',
      nombre: 'Corredora',
      descripcion: 'Formulario para cargos de corredora',
      fechaCreacion: '2024-04-01',
      fechaLimite: '2024-12-15',
      activo: true,
      editableByBanco: false,
      asignaciones: [
        { bancoId: '1', bancoNombre: 'Banco de Chile', activo: true, editableByBanco: false },
        { bancoId: '2', bancoNombre: 'Banco Santander', activo: false, editableByBanco: false },
        { bancoId: '3', bancoNombre: 'Banco Estado', activo: true, editableByBanco: false }
      ]
    },
    {
      id: '5',
      nombre: 'Asset Management',
      descripcion: 'Formulario para cargos de asset management',
      fechaCreacion: '2024-05-01',
      fechaLimite: '2024-11-30',
      activo: true,
      editableByBanco: false,
      asignaciones: [
        { bancoId: '1', bancoNombre: 'Banco de Chile', activo: false, editableByBanco: false },
        { bancoId: '2', bancoNombre: 'Banco Santander', activo: true, editableByBanco: false },
        { bancoId: '3', bancoNombre: 'Banco Estado', activo: true, editableByBanco: false }
      ]
    }
  ]);

  // Formulario oculto que se mostrar al cargar Excel
  const formularioNegociacion: Formulario = {
    id: '2',
    nombre: 'Negociacin Internacional y Comercio',
    descripcion: 'Formulario para cargos de negociacin internacional y comercio',
    fechaCreacion: '2024-02-01',
    fechaLimite: '2024-11-30',
    activo: true,
    editableByBanco: false,
    columnas: [
      { id: '1', nombre: 'COD. CARGO', tipo: 'texto', requerido: true, editable: false },
      { id: '2', nombre: 'NOMBRE GENRICO DEL CARGO', tipo: 'texto', requerido: true, editable: false },
      { id: '3', nombre: 'NIVEL DEL CARGO (HAY, MERCER O OGS)', tipo: 'texto', requerido: false, editable: true },
      { id: '4', nombre: '$ BONO ANUAL PAGADO EL 2025 POR EL DESEMPEO DEL 2024', tipo: 'numero', requerido: true, editable: true },
      { id: '5', nombre: 'INCENTIVOS TRIMESTRALES/ MENSUALES PAGADOS EL 2024', tipo: 'numero', requerido: true, editable: true },
      { id: '6', nombre: '$ TOTAL COMISIONES PAGADAS EL 2024', tipo: 'numero', requerido: false, editable: false },
      { id: '7', nombre: 'FECHA DE NACIMIENTO (dd/mm/aaaa)', tipo: 'fecha', requerido: false, editable: true },
      { id: '8', nombre: 'GNERO (Masculino o Femenino)', tipo: 'genero', requerido: false, editable: true }
    ],
    asignaciones: [
      { bancoId: '1', bancoNombre: 'Banco de Chile', activo: true, editableByBanco: false },
      { bancoId: '2', bancoNombre: 'Banco Santander', activo: true, editableByBanco: false },
      { bancoId: '3', bancoNombre: 'Banco Estado', activo: true, editableByBanco: false }
    ]
  };

  const [modalNuevoFormulario, setModalNuevoFormulario] = useState(false);
  const [modalCargarExcel, setModalCargarExcel] = useState(false);
  const [modalAsignaciones, setModalAsignaciones] = useState(false);
  const [modalEstructuraFormulario, setModalEstructuraFormulario] = useState(false);
  const [formularioSeleccionado, setFormularioSeleccionado] = useState<Formulario | null>(null);
  
  // Estados para formularios
  const [nuevoFormulario, setNuevoFormulario] = useState({
    nombre: '',
    descripcion: '',
    fechaLimite: ''
  });

  const [archivoExcel, setArchivoExcel] = useState<File | null>(null);
  const [formularioRecienAgregado, setFormularioRecienAgregado] = useState<string | null>(null);

  // Simular algunos formularios completados para demostrar la funcionalidad
  useEffect(() => {
    const completedForms = JSON.parse(localStorage.getItem('completed_forms') || '{}');
    // Simular que Banco de Chile complet el formulario 1 y Banco Santander complet el formulario 3
    if (Object.keys(completedForms).length === 0) {
      const simulatedCompletions = {
        '1_1': true, // Formulario 1, Banco de Chile
        '3_2': true, // Formulario 3, Banco Santander
      };
      localStorage.setItem('completed_forms', JSON.stringify(simulatedCompletions));
    }
  }, []);

  const handleToggleFormulario = (formularioId: string) => {
    setFormularios(prev => prev.map(f => 
      f.id === formularioId ? { ...f, activo: !f.activo } : f
    ));
    toast.success('Estado del formulario actualizado');
  };

  const handleToggleAsignacion = (formularioId: string, bancoId: string) => {
    setFormularios(prev => prev.map(f => {
      if (f.id === formularioId) {
        return {
          ...f,
          asignaciones: f.asignaciones.map(a => 
            a.bancoId === bancoId ? { ...a, activo: !a.activo } : a
          )
        };
      }
      return f;
    }));
    toast.success('Asignacin actualizada');
  };

  const handleAgregarFormulario = () => {
    if (!nuevoFormulario.nombre || !nuevoFormulario.descripcion || !nuevoFormulario.fechaLimite) {
      toast.error('Por favor complete todos los campos requeridos');
      return;
    }

    const nuevoFormularioCompleto: Formulario = {
      id: Date.now().toString(),
      nombre: nuevoFormulario.nombre,
      descripcion: nuevoFormulario.descripcion,
      fechaCreacion: new Date().toISOString().split('T')[0],
      fechaLimite: nuevoFormulario.fechaLimite,
      activo: true,
      editableByBanco: false,
      asignaciones: [
        { bancoId: '1', bancoNombre: 'Banco de Chile', activo: true, editableByBanco: false },
        { bancoId: '2', bancoNombre: 'Banco Santander', activo: true, editableByBanco: false },
        { bancoId: '3', bancoNombre: 'Banco Estado', activo: true, editableByBanco: false }
      ]
    };

    setFormularios(prev => [...prev, nuevoFormularioCompleto]);
    setModalNuevoFormulario(false);
    setNuevoFormulario({
      nombre: '',
      descripcion: '',
      fechaLimite: ''
    });
    toast.success('Formulario agregado exitosamente');
  };

  const handleCargarExcel = () => {
    if (!archivoExcel) {
      toast.error('Por favor seleccione un archivo Excel');
      return;
    }

    // Simular la carga del formulario de Negociacin Internacional
    const formularioExiste = formularios.some(f => f.id === '2');
    if (!formularioExiste) {
      setFormularios(prev => {
        // Insertar el formulario en la posicin correcta (despus del primero)
        const nuevaLista = [...prev];
        nuevaLista.splice(1, 0, formularioNegociacion);
        return nuevaLista;
      });
      setFormularioRecienAgregado('2');
      // Quitar el highlight despus de 3 segundos
      setTimeout(() => setFormularioRecienAgregado(null), 3000);
      toast.success('Formulario "Negociacin Internacional y Comercio" cargado exitosamente desde Excel');
    } else {
      toast('El formulario ya existe en el sistema', { icon: '' });
    }
    
    setModalCargarExcel(false);
    setArchivoExcel(null);
  };

  const handleEliminarFormulario = (formularioId: string) => {
    if (window.confirm('Est seguro de que desea eliminar este formulario?')) {
      setFormularios(prev => prev.filter(f => f.id !== formularioId));
      toast.success('Formulario eliminado exitosamente');
    }
  };

  const abrirModalAsignaciones = (formulario: Formulario) => {
    setFormularioSeleccionado(formulario);
    setModalAsignaciones(true);
  };

  const abrirModalEstructura = (formulario: Formulario) => {
    setFormularioSeleccionado(formulario);
    setModalEstructuraFormulario(true);
  };

  // Funcin para verificar si un banco complet un formulario
  const isBankFormCompleted = (formularioId: string, bancoId: string): boolean => {
    // Simular verificacin de completado basado en localStorage
    const completedForms = JSON.parse(localStorage.getItem('completed_forms') || '{}');
    return completedForms[`${formularioId}_${bancoId}`] === true;
  };

  const handleToggleEditableByBanco = (formularioId: string, bancoId: string) => {
    setFormularios(prev => {
      const updated = prev.map(f => {
        if (f.id === formularioId) {
          return {
            ...f,
            asignaciones: f.asignaciones.map(a => 
              a.bancoId === bancoId ? { ...a, editableByBanco: !a.editableByBanco } : a
            )
          };
        }
        return f;
      });
      
      // Guardar configuracin en localStorage para que la lean los bancos
      const adminSettings = JSON.parse(localStorage.getItem('admin_form_settings') || '{}');
      const formulario = updated.find(f => f.id === formularioId);
      if (formulario) {
        const asignacion = formulario.asignaciones.find(a => a.bancoId === bancoId);
        if (asignacion) {
          if (!adminSettings[formularioId]) {
            adminSettings[formularioId] = {};
          }
          adminSettings[formularioId][bancoId] = {
            editableByBanco: asignacion.editableByBanco,
            lastUpdated: new Date().toISOString()
          };
          localStorage.setItem('admin_form_settings', JSON.stringify(adminSettings));
        }
      }
      
      return updated;
    });
    
    // Actualizar formularioSeleccionado para reflejar cambios en tiempo real
    if (formularioSeleccionado && formularioSeleccionado.id === formularioId) {
      setFormularioSeleccionado(prev => {
        if (!prev) return prev;
        return {
          ...prev,
          asignaciones: prev.asignaciones.map(a => 
            a.bancoId === bancoId ? { ...a, editableByBanco: !a.editableByBanco } : a
          )
        };
      });
    }
    
    toast.success('Permisos de edicin actualizados');
  };

  const handleUpdateColumnConfig = (columnId: string, field: keyof ColumnConfig, value: any) => {
    if (!formularioSeleccionado) return;
    
    const updatedFormulario = {
      ...formularioSeleccionado,
      columnas: formularioSeleccionado.columnas?.map(col => 
        col.id === columnId ? { ...col, [field]: value } : col
      ) || []
    };
    
    setFormularioSeleccionado(updatedFormulario);
    setFormularios(prev => prev.map(f => 
      f.id === formularioSeleccionado.id ? updatedFormulario : f
    ));
  };

  const handleArchivoSeleccionado = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file && file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') {
      setArchivoExcel(file);
    } else {
      toast.error('Por favor seleccione un archivo Excel vlido (.xlsx)');
    }
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar>
          <IconButton color="inherit" onClick={() => navigate('/admin')}>
            <ArrowBackIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Gestin de Formularios
          </Typography>
        </Toolbar>
      </AppBar>

      <Container maxWidth="xl" sx={{ mt: 4, mb: 4 }}>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
          <Typography variant="h4">
            Gestin de Formularios
          </Typography>
          <Box sx={{ display: 'flex', gap: 2 }}>
            <Button
              variant="outlined"
              startIcon={<CloudUploadIcon />}
              onClick={() => setModalCargarExcel(true)}
            >
              Cargar Excel
            </Button>
            <Button
              variant="contained"
              startIcon={<AddIcon />}
              onClick={() => setModalNuevoFormulario(true)}
            >
              Nuevo Formulario
            </Button>
          </Box>
        </Box>

        {/* Estadsticas */}
        <Grid container spacing={3} sx={{ mb: 4 }}>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography variant="h4" color="primary">
                  {formularios.length}
                </Typography>
                <Typography color="textSecondary">
                  Total de Formularios
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography variant="h4" color="success.main">
                  {formularios.filter(f => f.activo).length}
                </Typography>
                <Typography color="textSecondary">
                  Formularios Activos
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography variant="h4" color="warning.main">
                  {formularios.filter(f => !f.activo).length}
                </Typography>
                <Typography color="textSecondary">
                  Formularios Inactivos
                </Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography variant="h4" color="info.main">
                  {formularios.reduce((total, f) => total + f.asignaciones.filter(a => a.activo).length, 0)}
                </Typography>
                <Typography color="textSecondary">
                  Asignaciones Activas
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>

        {/* Lista de formularios */}
        <Grid container spacing={3}>
          {formularios.map((formulario) => (
            <Grid item xs={12} md={6} key={formulario.id}>
              <Card
                sx={{
                  backgroundColor: formularioRecienAgregado === formulario.id ? '#e8f5e8' : 'inherit',
                  border: formularioRecienAgregado === formulario.id ? '2px solid #4caf50' : 'inherit',
                  transition: 'all 0.3s ease'
                }}
              >
                <CardContent>
                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 2 }}>
                    <Typography variant="h6" component="h2">
                      {formulario.nombre}
                    </Typography>
                    <Chip 
                      label={formulario.activo ? 'Activo' : 'Inactivo'} 
                      color={formulario.activo ? 'success' : 'default'}
                      size="small"
                    />
                  </Box>
                  
                  <Typography variant="body2" color="textSecondary" sx={{ mb: 2 }}>
                    {formulario.descripcion}
                  </Typography>

                                     <Grid container spacing={2} sx={{ mb: 2 }}>
                     <Grid item xs={6}>
                       <Typography variant="body2" color="textSecondary">
                         <strong>Creado:</strong> {formulario.fechaCreacion}
                       </Typography>
                     </Grid>
                     <Grid item xs={6}>
                       <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                         <Typography variant="body2" color="textSecondary">
                           <strong>Lmite:</strong>
                         </Typography>
                         <TextField
                           type="date"
                           size="small"
                           value={formulario.fechaLimite}
                           onChange={(e) => {
                             setFormularios(prev => prev.map(f => 
                               f.id === formulario.id ? { ...f, fechaLimite: e.target.value } : f
                             ));
                           }}
                           sx={{ width: '140px' }}
                         />
                       </Box>
                     </Grid>
                   </Grid>

                  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                    <Typography variant="body2" color="textSecondary">
                      Estado del Formulario
                    </Typography>
                    <Switch
                      checked={formulario.activo}
                      onChange={() => handleToggleFormulario(formulario.id)}
                      color="success"
                    />
                  </Box>
                </CardContent>

                <CardActions>
                  <Grid container spacing={1}>
                    <Grid item xs={6}>
                      <Button
                        size="small"
                        variant="outlined"
                        startIcon={<SettingsIcon />}
                        onClick={() => abrirModalAsignaciones(formulario)}
                        fullWidth
                      >
                        Gestionar Asignaciones
                      </Button>
                    </Grid>
                    <Grid item xs={6}>
                      <Button
                        size="small"
                        variant="outlined"
                        startIcon={<DescriptionIcon />}
                        onClick={() => abrirModalEstructura(formulario)}
                        fullWidth
                      >
                        Estructura
                      </Button>
                    </Grid>
                  </Grid>
                </CardActions>
              </Card>
            </Grid>
          ))}
        </Grid>

        {/* Modal Nuevo Formulario */}
        <Dialog open={modalNuevoFormulario} onClose={() => setModalNuevoFormulario(false)} maxWidth="sm" fullWidth>
          <DialogTitle>Crear Nuevo Formulario</DialogTitle>
          <DialogContent>
            <Grid container spacing={2} sx={{ mt: 1 }}>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label="Nombre del Formulario"
                  value={nuevoFormulario.nombre}
                  onChange={(e) => setNuevoFormulario({...nuevoFormulario, nombre: e.target.value})}
                  required
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label="Descripcin"
                  multiline
                  rows={3}
                  value={nuevoFormulario.descripcion}
                  onChange={(e) => setNuevoFormulario({...nuevoFormulario, descripcion: e.target.value})}
                  required
                />
              </Grid>
              <Grid item xs={12}>
                <TextField
                  fullWidth
                  label="Fecha Lmite"
                  type="date"
                  value={nuevoFormulario.fechaLimite}
                  onChange={(e) => setNuevoFormulario({...nuevoFormulario, fechaLimite: e.target.value})}
                  required
                  InputLabelProps={{ shrink: true }}
                />
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setModalNuevoFormulario(false)}>Cancelar</Button>
            <Button onClick={handleAgregarFormulario} variant="contained">
              Crear Formulario
            </Button>
          </DialogActions>
        </Dialog>

        {/* Modal Cargar Excel */}
        <Dialog open={modalCargarExcel} onClose={() => setModalCargarExcel(false)} maxWidth="sm" fullWidth>
          <DialogTitle>Cargar Formulario desde Excel</DialogTitle>
          <DialogContent>
            <Alert severity="info" sx={{ mb: 2 }}>
              Seleccione un archivo Excel (.xlsx) para crear un nuevo formulario
            </Alert>
            <Box sx={{ textAlign: 'center', py: 3 }}>
              <input
                accept=".xlsx,.xls"
                style={{ display: 'none' }}
                id="excel-file-input"
                type="file"
                onChange={handleArchivoSeleccionado}
              />
              <label htmlFor="excel-file-input">
                <Button
                  variant="outlined"
                  component="span"
                  startIcon={<UploadIcon />}
                  size="large"
                >
                  Seleccionar Archivo Excel
                </Button>
              </label>
              {archivoExcel && (
                <Typography variant="body2" sx={{ mt: 2 }}>
                  Archivo seleccionado: {archivoExcel.name}
                </Typography>
              )}
            </Box>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setModalCargarExcel(false)}>Cancelar</Button>
            <Button 
              onClick={handleCargarExcel} 
              variant="contained"
              disabled={!archivoExcel}
            >
              Cargar Formulario
            </Button>
          </DialogActions>
        </Dialog>

        {/* Modal Gestionar Asignaciones */}
        <Dialog open={modalAsignaciones} onClose={() => setModalAsignaciones(false)} maxWidth="md" fullWidth>
          <DialogTitle>
            Gestionar Asignaciones - {formularioSeleccionado?.nombre}
          </DialogTitle>
          <DialogContent>
            {formularioSeleccionado && (
              <Box sx={{ mt: 2 }}>
                <Alert severity="info" sx={{ mb: 3 }}>
                  <Typography variant="body2">
                    <strong>Nota:</strong> Los permisos de edicin solo se pueden activar para bancos que hayan completado el formulario.
                    Los bancos con formularios incompletos no podrn editar hasta completar la primera versin.
                  </Typography>
                </Alert>
                
                <Typography variant="h6" gutterBottom>
                  Asignaciones y Permisos por Banco
                </Typography>
                <Grid container spacing={2}>
                  {formularioSeleccionado.asignaciones.map((asignacion) => {
                    const isCompleted = isBankFormCompleted(formularioSeleccionado.id, asignacion.bancoId);
                    return (
                      <Grid item xs={12} key={asignacion.bancoId}>
                        <Box sx={{ 
                          p: 2,
                          border: '1px solid #e0e0e0',
                          borderRadius: 1,
                          backgroundColor: isCompleted ? '#f8f9fa' : '#fff'
                        }}>
                          <Grid container spacing={2} alignItems="center">
                            <Grid item xs={4}>
                              <Typography variant="body1" fontWeight="medium">
                                {asignacion.bancoNombre}
                              </Typography>
                              <Typography variant="caption" color="textSecondary">
                                {isCompleted ? 'Formulario completado' : 'Formulario pendiente'}
                              </Typography>
                            </Grid>
                            <Grid item xs={3}>
                              <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <Typography variant="caption" color="textSecondary">
                                  Asignado
                                </Typography>
                                <Switch
                                  checked={asignacion.activo}
                                  onChange={() => handleToggleAsignacion(formularioSeleccionado.id, asignacion.bancoId)}
                                  color="success"
                                  size="small"
                                />
                              </Box>
                            </Grid>
                            <Grid item xs={5}>
                              <Box sx={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
                                <Typography variant="caption" color="textSecondary">
                                  Permitir Editar
                                </Typography>
                                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                  <Switch
                                    checked={asignacion.editableByBanco}
                                    onChange={() => handleToggleEditableByBanco(formularioSeleccionado.id, asignacion.bancoId)}
                                    color="warning"
                                    size="small"
                                    disabled={!isCompleted || !asignacion.activo}
                                  />
                                  {!isCompleted && (
                                    <Tooltip title="El banco debe completar el formulario primero">
                                      <Typography variant="caption" color="textSecondary">
                                        (Requiere completar)
                                      </Typography>
                                    </Tooltip>
                                  )}
                                </Box>
                              </Box>
                            </Grid>
                          </Grid>
                        </Box>
                      </Grid>
                    );
                  })}
                </Grid>
              </Box>
            )}
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setModalAsignaciones(false)}>Cerrar</Button>
          </DialogActions>
        </Dialog>

        {/* Modal Estructura del Formulario */}
        <Dialog open={modalEstructuraFormulario} onClose={() => setModalEstructuraFormulario(false)} maxWidth="lg" fullWidth>
          <DialogTitle>
            Estructura del Formulario - {formularioSeleccionado?.nombre}
          </DialogTitle>
          <DialogContent>
            {formularioSeleccionado && (
              <Box sx={{ mt: 2 }}>
                <Typography variant="h6" gutterBottom>
                  Columnas y Tipos de Datos
                </Typography>
                {formularioSeleccionado.columnas && formularioSeleccionado.columnas.length > 0 ? (
                  <TableContainer component={Paper}>
                    <Table>
                      <TableHead>
                        <TableRow>
                          <TableCell><strong>Columna</strong></TableCell>
                          <TableCell><strong>Tipo de Dato</strong></TableCell>
                          <TableCell><strong>Requerido</strong></TableCell>
                          <TableCell><strong>Editable</strong></TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {formularioSeleccionado.columnas.map((columna) => (
                          <TableRow key={columna.id}>
                            <TableCell>
                              <Typography variant="body2">
                                {columna.nombre}
                              </Typography>
                            </TableCell>
                            <TableCell>
                              <FormControl size="small" sx={{ minWidth: 120 }}>
                                <Select
                                  value={columna.tipo}
                                  onChange={(e) => handleUpdateColumnConfig(columna.id, 'tipo', e.target.value)}
                                >
                                  <MenuItem value="texto">texto</MenuItem>
                                  <MenuItem value="numero">nmero</MenuItem>
                                  <MenuItem value="fecha">fecha</MenuItem>
                                  <MenuItem value="genero">gnero</MenuItem>
                                </Select>
                              </FormControl>
                            </TableCell>
                            <TableCell>
                              <Checkbox
                                checked={columna.requerido}
                                onChange={(e) => handleUpdateColumnConfig(columna.id, 'requerido', e.target.checked)}
                                color="error"
                              />
                            </TableCell>
                            <TableCell>
                              <Checkbox
                                checked={columna.editable}
                                onChange={(e) => handleUpdateColumnConfig(columna.id, 'editable', e.target.checked)}
                                color="success"
                              />
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                ) : (
                  <Alert severity="info">
                    Este formulario no tiene columnas configuradas. Las columnas se cargan automticamente cuando se importa un archivo Excel.
                  </Alert>
                )}
              </Box>
            )}
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setModalEstructuraFormulario(false)}>Cerrar</Button>
          </DialogActions>
        </Dialog>
      </Container>
    </Box>
  );
};

export default GestionFormularios;
